.button {
    width: 132px;
    height: 30px;
    display: inline-flex;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border: transparent;
    border-radius: 3px;

    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px; /* 142.857% */
    cursor: pointer;
}

.disabled {
    opacity: 0.5;
    background: #004D99;
    color: #002E5C;
}

.secondary {
    border: 1px solid #06C;
    width: 175px;
    background: transparent;
    color: #06C;
}

.default {
    background: #06C;
    color: #FFF;
}

.red {
    background: #CC1439;
    color: #FFF;
}

.default:hover {
    width: 165px;
    background: #0080FF;
}

.red:hover {
    width: 165px;
    background: #FF1A47;
}

.secondary:hover {
    width: 165px;
    background: #E5F0FA;
}


.default:active {
    width: 150px;
    background: #0059B2;
}

.secondary:active {
    width: 150px;
    background: #C9E5FF;
}

.red:active {
    width: 150px;
    background: #B21232;
}

.button:disabled {
    cursor: auto;
}
