.sidebar {
    display: none;
}

.sidebar.open {
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 320px;
    height: 100%;
    padding: 15px 25px 0 70px;
    /*min-height: 1080px;*/
    background-color: #fff;
    z-index: 9999;
}

.background {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 9998;
    background-color: rgba(0,0,0,.4)
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
}

.nav a {
    text-decoration: none;
    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a:active {
    color: #3D6B99;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}

.close {
    background: transparent;
    border: none;
    padding-bottom: 25px;
    align-self: end;
}

.close img {
    width: 24px;
    cursor: pointer;
}
